
.scroll-top {
    position: fixed;
    bottom: 2em;
    right: 1em;
    background-color: transparent;
  }
  @keyframes bounce {
    0%, 100% {
      transform: translateY(0);
    }
    50% {
      transform: translateY(-10px);
    }
  }
  
  .scroll-top {
    animation: bounce 2s infinite;
  }
  @media (max-width: 900px) {
    .scroll-top {
      display: none;
    }
  }